import { Component } from '@angular/core';
import { MatIconRegistry } from "@angular/material/icon";
import { DomSanitizer } from "@angular/platform-browser";
import { TranslateService } from '@ngx-translate/core';


@Component({
  selector: 'app-root',
  templateUrl: './app.component.html',
  styleUrls: ['./app.component.scss']
})
export class AppComponent {

  constructor(private matIconRegistry: MatIconRegistry,
    private domSanitizer: DomSanitizer,
    public translate: TranslateService

    ){

      
      var userLang = navigator.language; 
      console.log(userLang.substring(0, 2));
      
      
      translate.addLangs(['en', 'fr']);
      // translate.setDefaultLang("fr");
      translate.setDefaultLang(userLang.substring(0, 2));



      this.matIconRegistry.addSvgIcon(
        "my_icon_users_gray",
        this.domSanitizer.bypassSecurityTrustResourceUrl("../assets/svg/user_gray.svg")
      ); 

      this.matIconRegistry.addSvgIcon(
        "analytics_gray",
        this.domSanitizer.bypassSecurityTrustResourceUrl("../assets/svg/analytics_gray.svg")
      ); 

      this.matIconRegistry.addSvgIcon(
        "app_gray",
        this.domSanitizer.bypassSecurityTrustResourceUrl("../assets/svg/app_gray.svg")
      ); 

      this.matIconRegistry.addSvgIcon(
        "dashboard_gray",
        this.domSanitizer.bypassSecurityTrustResourceUrl("../assets/svg/dashboard_gray.svg")
      ); 

      this.matIconRegistry.addSvgIcon(
        "deconnecting_gray",
        this.domSanitizer.bypassSecurityTrustResourceUrl("../assets/svg/deconnecting_gray.svg")
      ); 

      this.matIconRegistry.addSvgIcon(
        "parameters_gray",
        this.domSanitizer.bypassSecurityTrustResourceUrl("../assets/svg/parameters_gray.svg")
      ); 

      this.matIconRegistry.addSvgIcon(
        "validation_gray",
        this.domSanitizer.bypassSecurityTrustResourceUrl("../assets/svg/validation_gray.svg")
      ); 

   

    
    }


}
