import { Component, OnInit } from '@angular/core';
import { MatBottomSheetRef } from '@angular/material/bottom-sheet';

@Component({
  selector: 'app-bottomsheet-symptoms',
  templateUrl: './bottomsheet-symptoms.component.html',
  styleUrls: ['./bottomsheet-symptoms.component.scss']
})
export class BottomsheetSymptomsComponent implements OnInit {
  constructor(private _bottomSheetRef: MatBottomSheetRef<BottomsheetSymptomsComponent>) {}

  openLink(event: MouseEvent): void {
    this._bottomSheetRef.dismiss();
    event.preventDefault();
  }


  ngOnInit(): void {
  }

}
